.building-blocks .checking-example {
  width: 40vw;
  margin: 0 auto;
  background: #c7c6ff;
  border: 2px solid #4947ad;
  padding: 0; }
  @media screen and (min-width: 1080px) {
    .building-blocks .checking-example {
      width: 30vw; } }
  .building-blocks .checking-example > * {
    width: 100%;
    padding: 3vw 0;
    display: block; }
  .building-blocks .checking-example .app-title {
    font-size: 5vw;
    background: white;
    font-weight: bold;
    border-bottom: 2px solid #4947ad; }
    @media screen and (min-width: 1080px) {
      .building-blocks .checking-example .app-title {
        font-size: 3vw; } }
  .building-blocks .checking-example .balance {
    font-weight: bold;
    font-size: 3.5vw;
    color: #ff4141;
    height: 20vw;
    line-height: 20vw; }
    @media screen and (min-width: 1080px) {
      .building-blocks .checking-example .balance {
        font-size: 2vw; } }
    .building-blocks .checking-example .balance button {
      font-size: 1.5em; }
  .building-blocks .checking-example .info {
    font-size: 3vw;
    border-top: 2px solid #4947ad;
    background-color: white; }
    @media screen and (min-width: 1080px) {
      .building-blocks .checking-example .info {
        font-size: 1.75vw; } }
