.sandbox {
  margin: 2vw auto 4vw auto;
  padding: 1vw;
  display: block;
  overflow: hidden;
  border-radius: 1vw;
  border-bottom-left-radius: 0;
  border-top-right-radius: 0;
  background-color: black;
  color: white;
  position: relative;
  width: 100%;
  height: 50vh;
  min-height: 400px;
  transform: none; }
  @media screen and (max-width: 640px) {
    .sandbox {
      margin-left: -5vw; } }
  .sandbox .icon {
    font-size: 40px;
    position: absolute;
    top: 0%;
    user-select: none;
    display: inline-block;
    width: auto;
    height: auto;
    line-height: 50px;
    cursor: pointer;
    transition: color 0.5s ease; }
    .sandbox .icon.play {
      left: 4%; }
    .sandbox .icon.reset {
      right: 4%; }
    .sandbox .icon:active {
      color: white;
      transition: none; }
  @media screen and (max-width: 640px) {
    .sandbox {
      width: 90vw;
      font-size: 0.7em; } }
  .sandbox.short {
    height: 20vh;
    min-height: 250px; }
  .sandbox > * {
    display: block; }
  .sandbox > * > * {
    display: inline-block;
    width: 50%;
    height: 100%; }
  .sandbox .main-title {
    position: relative;
    font-size: 1.5em !important;
    height: 50px;
    line-height: 50px;
    background-color: #202020;
    color: #39ff14; }
  .sandbox .titles {
    height: 50px;
    line-height: 50px;
    width: 100%;
    text-align: center;
    position: relative; }
  .sandbox .inner {
    width: 100%;
    height: calc(100% - 100px);
    position: relative; }
    .sandbox .inner > * {
      position: absolute;
      top: 0;
      left: 0; }
      .sandbox .inner > *:nth-child(2) {
        left: 50%; }
  .sandbox .output:not(.valid) {
    background-color: #202020;
    padding: 20px;
    width: calc(50% - 40px);
    height: calc(100% - 40px); }
  .sandbox .output.error {
    color: #ff0068; }
