@font-face {
  font-family: 'Droid';
  src: url("/static/fonts/droidSerifRegular.ttf");
  font-weight: normal;
  font-display: swap; }

@font-face {
  font-family: 'Droid';
  src: url("/static/fonts/droidSerifBold.ttf");
  font-weight: bold;
  font-display: swap; }

* {
  margin: 0;
  padding: 0; }

body {
  position: relative;
  font-family: 'Droid';
  background-color: white;
  color: black;
  min-height: 100vh;
  min-width: 100vw;
  background-color: white; }
  @media screen and (min-width: 640px) {
    body {
      background-attachment: fixed;
      background-image: url("data:image/svg+xml,%3Csvg width='40' height='12' viewBox='0 0 40 12' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0 6.172L6.172 0h5.656L0 11.828V6.172zm40 5.656L28.172 0h5.656L40 6.172v5.656zM6.172 12l12-12h3.656l12 12h-5.656L20 3.828 11.828 12H6.172zm12 0L20 10.172 21.828 12h-3.656z' fill='%23dae2ff' fill-opacity='0.4' fill-rule='evenodd'/%3E%3C/svg%3E"); } }

#next {
  display: block;
  width: 100%;
  height: 100%; }

#page {
  text-align: center;
  margin: 0 8vw;
  font-size: 2vw;
  padding: 5vh 10vw 10vh 10vw;
  min-height: 85vh;
  background-color: white; }
  @media screen and (max-width: 640px) {
    #page {
      margin: 0 1vw;
      font-size: 5vw; } }
  @media screen and (min-width: 1080px) {
    #page {
      font-size: 1.25vw; } }
  @media screen and (min-width: 640px) {
    #page {
      box-shadow: 0 0 10px #d1d1ff; } }
  #page > .title, #page > * > .title {
    text-shadow: #d1d1ff 1px 1px 7px;
    font-size: 4vw; }
  #page h3 {
    font-size: 1.5em; }
  #page .intro-card {
    display: block;
    text-align: center;
    font-style: italic;
    margin: 20px auto; }
    #page .intro-card > *:not(.youtube-container) {
      display: block;
      width: 80%;
      min-width: 300px;
      border-radius: 10px;
      margin: 20px auto; }
    #page .intro-card a {
      display: inline; }
  #page a:visited, #page a:link {
    color: #4747af; }
  #page .smaller-text {
    font-size: 0.75em; }
  #page p {
    margin: 20px 0; }
  #page .bold {
    font-weight: bold; }
  #page .underline {
    text-decoration: underline; }
  #page .italic {
    font-style: italic; }
  #page .toc {
    border-bottom: 8px solid black;
    border-radius: 10px;
    margin: 50px auto;
    font-size: 1.8em;
    list-style: none;
    display: table;
    counter-reset: toc-counter; }
    #page .toc.pages {
      font-size: 1.2em;
      list-style: disc; }
    #page .toc > h2 {
      font-style: italic; }
    #page .toc li {
      margin: 10px 0;
      font-size: 1.4em;
      text-align: left;
      counter-increment: toc-counter; }
    #page .toc.sections li::before {
      margin-right: 10px;
      content: counter(toc-counter) " ";
      color: #4747af;
      font-weight: bold; }
  #page a {
    color: #4747af; }
  #page li a {
    text-decoration: none; }
  #page .youtube-container {
    position: relative;
    width: 80%;
    margin: 20px auto;
    height: 0;
    padding-bottom: 56.25%; }
    @media screen and (max-width: 640px) {
      #page .youtube-container {
        width: 100%; } }
    #page .youtube-container iframe {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%; }

.next {
  display: inline-block;
  border: 4px solid black;
  padding: 1vw 2vw;
  font-size: 4.5vw;
  font-weight: bold;
  text-align: center;
  width: auto;
  margin: 40px 0 20px auto; }
